@import url(https://fonts.googleapis.com/css2?family=Saira:wght@400;500;700&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 14px;
    font-family: 'Saira', sans-serif;
}
h1, .pokemon-list{
    width: 80vw;
    margin: auto;
}

h1{
    font-size: 30px;
    color: #909090;
    font-weight: 500;
}

.pokemon-list, .header{
    display: flex;
    justify-content: center;
}
.pokemon-list{
    flex-wrap: wrap;
}

.header{
    height: 100px;
    display: flex;
    align-items: center;
    background-color: #313131;
}

.header nav{
    width: 80vw;
}

.header input{
    width: 100%;
    border-radius: 8px;
    height: 35px;
    padding: 0 10px ;
}
.pokemonCard{
    border-radius: 8px;
    margin: 15px;
    max-width: 180px;
    
    transition: all ease 0.3s;

}

.pokemonCard:hover{
    box-shadow: rgb(0 0 0 / 30%) 5px 5px 10px, rgb(255 255 255) -5px -5px 10px 5px;
}

.pokemonImage{
    display: flex;
    flex-direction: column;
    width: 180px;
    background-color: #f2f2f2 ;
    border-radius: 10px;
}

.pokemonImage img{
    align-self: center;
    margin: 15px;
    width: 120px;
    height: 120px;
}

.pokemonImage p, .pokemonType p{
    margin: 5px;
    text-align: center;
    border-radius: 10px;
    font-size: 12px;
}

.pokemonImage p{
    background-color: white;
    display: inline;
    width: 25%;
}

h2{
    font-size: 19px;
    font-weight: 400;
    text-align: center;
}

.pokemonType{
    display: flex;    
}

.pokemonType p{
    width: 50%;
}
